@import "../../scss/variables";


.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    .cards{
        background-color: $cardNavBgColor;
        margin: 1rem;
        padding: 1rem;
        border-radius: 7px;
        box-shadow: 3px 3px 5px rgba(0,0,0,0.7);
        position: relative;
        overflow: hidden;
        &:hover .card-over{
            transform: translateY(0%);
            z-index: 1;

        }
    }
    .title{
        color:$backgroundColor;
        padding: 1rem;
        font-size: 1.5rem;
    }

    img{
    width: 350px;
    height: 250px;;
    object-fit: cover;
    filter:grayscale(0%);
    &:hover{
        filter: grayscale(100%);
    }

    }
    .card-over{
        color: white;
        position: absolute;
        bottom: 0;
        left:0;
        background-color: rgba(0,0,0,0.5);
        font-size: 1.5rem;
        padding: 1rem;
        transform: translateY(100%);
        z-index: 1;
        transition: transform 0.7s ease-out ;
        
        }
   
}

